* {
  box-sizing: border-box;
  font-family: "Source Sans Pro", sans-serif;
}

a,
a:visited {
  color: #000000;
  cursor: pointer;
}

body {
  background-color: #ffffff;
  width: 100vw;
  overflow-x: hidden;
}

.header {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}